
.bg {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	overflow: hidden;
	background: url(/static/bg.jpg) center center no-repeat;
	background-size: cover;
}
.btn-box {
	position: absolute;
	top: 616px;
	left: 460px;
	width: 160px;
	height: 48px;
	line-height: 48px;
	font-size: 26px;
	color: #c50000;
	font-weight: 400;
	letter-spacing: 10px;
	background: url(/static/button.png) center center no-repeat;
	background-size: 100% auto;
	cursor: pointer;
}
.msg {
	position: absolute;
	top: 50%;
	left: 0;
	right: 0;
	text-align: center;
	line-height: 50px;
	height: 50px;
	margin-top: -25px;
}
.msg span {
	display: inline-block;
	padding: 0 20px;
	color: #fff;
	background: rgba(0, 0, 0, 0.8);
	line-height: 40px;
	font-size: 20px;
}
.audio-box {
	position: absolute;
	width: 10px;
	height: 10px;
	overflow: hidden;
	top: -100px;
	left: -100px;
}
.preview {
	position: fixed;
	z-index: 9998;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.5);
	display: table;
	-webkit-transition: opacity 0.3s ease;
	transition: opacity 0.3s ease;
}
.preview-wrapper {
	display: table-cell;
	vertical-align: middle;
}
.preview-container {
	width: 440px;
	height: 150px;
	margin: 0px auto;
	background-color: #fff;
	border-radius: 2px;
	-webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
	        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
	font-family: Helvetica, Arial, sans-serif;
	position: relative;
}
.confirm {
	position: absolute;
	right: 10px;
	top: 0px;
	font-size: 40px;
}
.confirm:hover {
	color: red;
	cursor: pointer;
}
.preview-body {
	position: absolute;
	width: 420px;
	height: 130px;
	margin: 10px 10px 10px 10px;
}
.preview-body>h4 {
	margin-top: 40px;
}
.preview-body>button {
	position: absolute;
	right: 10px;
	bottom: 0px;
}
.green_color {
	color: green;
}
.red_color {
	color: red;
}



